footer {
    position: fixed;
    height: 10rem;
    bottom: 0;
    padding: 0 .5rem;
    width: 100%;
    background-color: #EB5757;
    .reproductor {
        display: flex;
        // max-width: 120rem;
        margin: 0 auto;
        justify-content: space-between;
        align-items: center;
        height: 100%;

        img {
            width: 10rem;
            
        }

        button {
            height: 50%;
        }

        audio {
            display: block;
            
        }

        .fpart1 {
            display: flex;
            height: 100%;

            .fpart1-info {

                margin-left: .5rem;
                p {
                    color: white;
                }
                p:first-child {
                    font-size: 1.4rem;
                    font-weight: 700;
                }

                p:last-child {
                    font-size: 1.2rem;
                    font-weight: 400;
                }
            }
        }
        .fpart3 {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 2rem;
            input {
                margin-right: 2rem;
                width: 10rem;
            }
            .parlantes {
                width: 4rem;
            }

            .parlante {
                background: none;
                border: none;
                cursor: pointer;
            }

            .parlanteOff {
                font-size: 3.5rem;
                color: white;
            }
        }
        .fpart2 {
            display: flex;
            justify-content: center;
            align-items: center;
            .play,.pause {
                height: 6rem;
                width: 6rem;
                
                border-radius: 50%;
                border: none;
                background-color: #FF7676;
                cursor: pointer;
                
            }

            .left,.right {
                background: none;
                border: none;
                cursor: pointer;
            }

            i {
                color: white;
            }
        }
    }
}

.rango {
    -webkit-appearance: none;
    background-color: white;
    border-radius: 2rem;
    height: .5rem;
}
/**chorme **/
.rango::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 2rem;
    height: 2rem;
    background: white;
    border-radius: 50%;
    cursor: pointer;
}
/** mozilla*/
.rango::-moz-range-thumb {
    width: 2rem;
    height: 2rem;
    background: white;
    border-radius: 50%;
    cursor: pointer;    
}
.none {
    display: none;
}

.show {
    display: block;
}

.volumen {
    display: flex;
    justify-content: center;
    align-items: center;

    .vol {
        margin-right: 1rem;
        color: white;
        font-weight: 700;
        width: 4rem;
    }
}