.text-center {
  text-align: center; }

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Quicksand", sans-serif;
  font-size: 1.6rem;
  zoom: 70%; }

p {
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

h1, h2, h3 {
  font-family: "Quicksand", sans-serif;
  margin: 0;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

/** Section2 **/
.sitio {
  display: flex;
  height: 100%;
  margin-bottom: 30rem; }

header {
  width: 33rem;
  padding: 2rem 1.5rem;
  display: none;
  background-color: #662323;
  height: 100%;
  position: fixed; }
  @media (min-width: 1024px) {
    header {
      display: block; } }
  header .div-logo {
    width: 100%; }
  header .logo {
    width: 80%;
    margin: auto; }

ul {
  margin: 0;
  padding: 0; }

.sitio ul li {
  list-style-type: none;
  color: white;
  font-weight: 700;
  font-size: 2.2rem; }

.sitio ul ul li {
  font-size: 1.6rem;
  font-weight: 400; }

li {
  margin: 1rem 0; }

.body-page {
  padding: 2rem 5rem;
  margin-left: 30rem; }

.section1 {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .section1 .buscar {
    cursor: pointer; }
  .section1 .busqueda {
    width: 52.4rem;
    border: 1px solid #BDBDBD;
    display: flex;
    justify-content: space-around;
    border-radius: 10rem;
    padding: 1rem; }
    .section1 .busqueda input {
      width: 90%;
      border: none;
      outline: none; }
  .section1 picture {
    display: flex; }

.profile {
  display: flex;
  align-items: center; }
  .profile picture {
    margin-right: 1rem; }
  .profile p {
    color: #4F4F4F;
    font-size: 1.6rem;
    font-weight: 400; }

.section2 {
  width: 100%;
  display: flex;
  height: 25rem;
  margin-top: 3rem; }
  .section2 .btn-image {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 25rem;
    height: 25rem;
    background-image: url("../img/adele.webp");
    background-repeat: no-repeat;
    background-size: contain; }
    .section2 .btn-image button {
      background: none;
      border: none;
      cursor: pointer; }

.contenedor-info-artist {
  background-image: url("../img/fondo.webp");
  background-size: cover;
  background-repeat: repeat;
  width: 64.2rem; }

.img-fondo {
  background-color: rgba(167, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  padding: 4rem 2.1rem 2rem 3rem; }

.info-artist {
  width: 100%;
  color: white; }
  .info-artist h3 {
    font-size: 2.2rem;
    font-weight: 700;
    text-align: left; }
  .info-artist .extra {
    display: flex;
    align-items: center; }
    .info-artist .extra p {
      margin: 0; }
    .info-artist .extra p:first-child {
      font-size: 1.4rem; }
    .info-artist .extra p:last-child {
      font-size: 1rem;
      color: #662323;
      margin-left: 1rem; }
  .info-artist p {
    font-size: 1.2rem;
    line-height: 2rem; }
  .info-artist .botones-info {
    display: flex;
    align-items: center; }
    .info-artist .botones-info button {
      background-color: #E86060;
      color: white;
      border: none;
      height: 3.1rem;
      width: 11.7rem;
      border-radius: 10rem;
      font-size: 1.4rem;
      font-weight: 400;
      margin-right: 1.5rem;
      cursor: pointer; }
    .info-artist .botones-info button:nth-child(2) {
      background-color: transparent;
      border: 1px solid #EB5757;
      color: #E86060; }
    .info-artist .botones-info p {
      font-size: 3rem; }

.section2_play,
.section2_pause {
  color: white;
  font-size: 6rem; }
  .section2_play:hover,
  .section2_pause:hover {
    color: #E3E4E5;
    font-size: 7rem; }

.resultados {
  margin-top: 4rem;
  width: 89.2rem; }
  .resultados .musics {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    row-gap: 2.5rem;
    column-gap: 2rem; }
  .resultados .music {
    width: 100%;
    display: flex;
    flex-direction: column; }
    .resultados .music h3 {
      font-size: 1.4rem;
      font-weight: 700;
      text-align: left;
      margin-top: .5rem; }
    .resultados .music h4 {
      font-size: 1.2rem;
      font-weight: 400;
      margin: 0;
      margin-top: .5rem; }
  .resultados h2 {
    color: #E86060;
    text-align: left;
    font-weight: 700;
    font-size: 2.2rem;
    margin-bottom: 3rem; }
  .resultados .img-music {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 16rem;
    width: 100%; }
    .resultados .img-music img {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: -1; }
    .resultados .img-music .puntos {
      display: flex;
      flex-direction: column; }
      .resultados .img-music .puntos p {
        position: absolute;
        color: white;
        transform: rotate(90deg);
        right: 0;
        top: 1rem;
        margin: 0;
        width: 1rem; }
    .resultados .img-music button {
      border: none;
      height: 3.2rem;
      width: 3.6rem;
      background: none;
      cursor: pointer; }
      .resultados .img-music button svg {
        height: 100%;
        width: 100%; }

.pause {
  display: none; }

.btn-music,
.btn-music_paused {
  color: white;
  font-size: 3rem; }
  .btn-music:hover,
  .btn-music_paused:hover {
    color: #E3E4E5;
    font-size: 4rem; }

footer {
  position: fixed;
  height: 10rem;
  bottom: 0;
  padding: 0 .5rem;
  width: 100%;
  background-color: #EB5757; }
  footer .reproductor {
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
    height: 100%; }
    footer .reproductor img {
      width: 10rem; }
    footer .reproductor button {
      height: 50%; }
    footer .reproductor audio {
      display: block; }
    footer .reproductor .fpart1 {
      display: flex;
      height: 100%; }
      footer .reproductor .fpart1 .fpart1-info {
        margin-left: .5rem; }
        footer .reproductor .fpart1 .fpart1-info p {
          color: white; }
        footer .reproductor .fpart1 .fpart1-info p:first-child {
          font-size: 1.4rem;
          font-weight: 700; }
        footer .reproductor .fpart1 .fpart1-info p:last-child {
          font-size: 1.2rem;
          font-weight: 400; }
    footer .reproductor .fpart3 {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 2rem; }
      footer .reproductor .fpart3 input {
        margin-right: 2rem;
        width: 10rem; }
      footer .reproductor .fpart3 .parlantes {
        width: 4rem; }
      footer .reproductor .fpart3 .parlante {
        background: none;
        border: none;
        cursor: pointer; }
      footer .reproductor .fpart3 .parlanteOff {
        font-size: 3.5rem;
        color: white; }
    footer .reproductor .fpart2 {
      display: flex;
      justify-content: center;
      align-items: center; }
      footer .reproductor .fpart2 .play, footer .reproductor .fpart2 .pause {
        height: 6rem;
        width: 6rem;
        border-radius: 50%;
        border: none;
        background-color: #FF7676;
        cursor: pointer; }
      footer .reproductor .fpart2 .left, footer .reproductor .fpart2 .right {
        background: none;
        border: none;
        cursor: pointer; }
      footer .reproductor .fpart2 i {
        color: white; }

.rango {
  -webkit-appearance: none;
  background-color: white;
  border-radius: 2rem;
  height: .5rem; }

/**chorme **/
.rango::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2rem;
  height: 2rem;
  background: white;
  border-radius: 50%;
  cursor: pointer; }

/** mozilla*/
.rango::-moz-range-thumb {
  width: 2rem;
  height: 2rem;
  background: white;
  border-radius: 50%;
  cursor: pointer; }

.none {
  display: none; }

.show {
  display: block; }

.volumen {
  display: flex;
  justify-content: center;
  align-items: center; }
  .volumen .vol {
    margin-right: 1rem;
    color: white;
    font-weight: 700;
    width: 4rem; }
