.body-page {
    padding: 2rem 5rem;
    margin-left: 30rem;
}

.section1 {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .buscar {
        cursor: pointer;
    }
    .busqueda {
        width: 52.4rem;
        border: 1px solid #BDBDBD;
        display: flex;
        justify-content: space-around;
        border-radius: 10rem;
        padding: 1rem;

        input {
            width: 90%;
            border: none;
            outline: none;
            

        }
    }

    picture {
        display: flex;
    }
}

.profile {
    display: flex;
    align-items: center;

    picture {
        margin-right: 1rem;
    }

    p {
        color: #4F4F4F;
        font-size: 1.6rem;
        font-weight: $regular;
    }
    
}
