.resultados {

    margin-top: 4rem;
    width: 89.2rem;

    .musics {
        display: grid;
        grid-template-columns: repeat(5,1fr);
        row-gap: 2.5rem;
        column-gap: 2rem;
    }

    .music {
        width: 100%;
        display: flex;
        flex-direction: column;

        h3 {
            font-size: 1.4rem;
            font-weight: 700;
            text-align: left;
            margin-top: .5rem;
        }

        h4 {
            font-size: 1.2rem;
            font-weight: 400;
            margin: 0;
            margin-top: .5rem;
        }
    }

    h2 {
        color: #E86060;
        text-align: left;
        font-weight: 700;
        font-size: 2.2rem;
        margin-bottom   : 3rem;
    }

    .img-music {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 16rem;
        width: 100%;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: -1;
            
        }

        


        .puntos {
            display: flex;
            flex-direction: column;
            p {
                position: absolute;
                color: white;
                transform: rotate(90deg);
                right: 0;
                top: 1rem;
                margin: 0;
                width: 1rem;
                
            }
        }

        button {
            border: none;
            height: 3.2rem;
            width: 3.6rem;
            background: none;
            cursor: pointer;
            
            svg {
                height: 100%;
                width: 100%;
            }
        }
    }

}

.pause {
    display: none;
}


.btn-music,
.btn-music_paused {
    color: white;
    font-size: 3rem;

    &:hover {
        color: #E3E4E5;
        font-size: 4rem;
    }
}