.sitio {
    display: flex;
    height: 100%;
    margin-bottom: 30rem;
}

header {
    
    width: 33rem;
    padding: 2rem 1.5rem;
    display: none;
    background-color: #662323;
    height: 100%;
    position: fixed;

    @include desktop {
        display: block;
    }

    .div-logo {
        width: 100%;
     
    }
    .logo {
        width: 80%;
        margin: auto;
    }
}

ul {
    margin: 0;
    padding: 0;

   
}
.sitio ul li {
    list-style-type: none;
    color: white;
    font-weight: $superbold;
    font-size: 2.2rem;
}
.sitio ul ul li {
    font-size: 1.6rem;
    font-weight: $regular;
}

li {
    margin: 1rem 0;
}